

.footer {
    
left:0;
bottom:0;
right:0;
}

.time {
color: #F0FCFF;
}

.date {
color: #B3E6F9;
}

.addHabit {
    font-size: xx-large;
    text-align: right;
}

.addHabitButton {
    height: 20vw;
    width: 20vw;
    border-radius: 50%;
    border: 0px;
    background-color: #64CDC8;
    margin-right: 6vw;
}
.addHabitButton:hover {
    background-color: #6fe4de;
    box-shadow: 0 5px 15px rgba(145, 92, 182, .4);
}
.addHabitButton:active {
    box-shadow: 0 5px 15px rgba(145, 92, 182, .4);
    background-color: #54b1ac;
}

.closeHabitButton {
    height: 20vw;
    width: 20vw;
    border-radius: 50%;
    border: 0px;
    background-color: #FFA8A8;
    margin-right: 6vw;
}
.closeHabitButton:hover {
    background-color: #ffc5c5;
    box-shadow: 0 5px 15px rgba(145, 92, 182, .4);
}
.closeHabitButton:active {
    box-shadow: 0 5px 15px rgba(145, 92, 182, .4);
    background-color: #ff9393;
}